const Wrapper = styled.form`
display:flex;
flex-direction:column;
width: 70vw;
margin: 25px auto;
`

class RegisterUser extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            candidate: {

            }
        }
    }

    componentDidMount() {
        this.props.getTrips()
    }

    handleChange = e => {
        const { name, value } = e.target
        this.setState({
            candidate: {
                ...this.state.candidate, [name]: value
            }
        })
    }

    handleSubmit = e => {
        e.preventDefault()
        const candidateData = this.state.candidate
        const id = candidateData.trip

        const data = {
            name: candidateData.name,
            age: candidateData.age,
            applicationText: candidateData.applicationText,
            profession: candidateData.profession,
            country: candidateData.country
        }
        console.log(id,data)
        this.props.registerNewUser(id,data)
    }
    render() {
        return (

            <Wrapper onSubmit={this.handleSubmit}>
                <h2>Faça seu cadastro abaixo:</h2>
                <FormControl>
                    <InputLabel htmlFor="Nome-da-viagem"> Viagem</InputLabel>
                    <Select
                        value={this.state.candidate.trip}
                        onChange={this.handleChange}
                        name="trip"
                        inputProps={{
                            required: true
                        }}
                    >
                        {this.props.allTrips.map(el => {
                            return (
                                <MenuItem
                                    key={el.id}
                                    id={el.id}
                                    value={el.id}
                                >{el.planet}: {el.name}</MenuItem>
                            )
                        })}
                    </Select>
                </FormControl>
                {dataForm.map(field => {
                    return (
                        <TextField
                            onChange={this.handleChange}
                            key={field.name}
                            value={this.state.candidate[field.name]}
                            name={field.name}
                            label={field.label}
                            type={field.type}
                            required={field.required}
                            inputProps={{
                                pattern: field.pattern,
                                title: field.title,
                                min: field.min
                            }}
                        />
                    )
                })}
                <FormControl>
                    <InputLabel>País</InputLabel>
                    <Select
                        value={this.state.candidate.country}
                        onChange={this.handleChange}
                        name="country"
                    >
                        {country.map(el => {
                            return (
                                <MenuItem
                                    key={el.name}
                                    value={el.name}
                                >
                                    {el.name}
                                </MenuItem>
                            )
                        })}
                    </Select>
                </FormControl>
                <Button type="submit" color='primary'size= 'large'
                >Enviar</Button>
                <Button 
                color = 'secondary' size= 'large'
                onClick = {this.props.goToListTripsScreen}
                 >
                        Voltar
                </Button>

            </Wrapper>

        )
    }
}

const mapStateToProps = state => {
    return (
        {
            allTrips: state.trips.trips
        }
    )
}

const mapDispatchToProps = dispatch => {
    return (
        {
            getTrips: () => dispatch(getTrips()),
            registerNewUser:(id,data) => dispatch(registerNewUser(id,data)),
            goToListTripsScreen: () => dispatch(push(routes.listUserTrips))
        }
    )
}

export default connect(mapStateToProps, mapDispatchToProps)(RegisterUser)

